.scaleBox {
  position: relative;
  display: table;
  margin: auto;
  table-layout: fixed;
}
.scaleBox > span {
  position: relative;
  display: block;
  overflow: hidden;
  table-layout: fixed;
}
.scaleBox > span img {
  transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}
@media (hover: hover) {
  .scaleBox:hover > span img {
    transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1);
    transform: scale(1.05);
  }
}
.scaleBox:focus-visible > span img {
  transition: all 1s cubic-bezier(0.165, 0.84, 0.44, 1);
