    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}
.scrollIn,
.loadIn {
  opacity: 0;
  transition: all 800ms;
}
.scrollIn.up,
.loadIn.up {
  transform: translate(0, 50px);
}
.scrollIn.down,
.loadIn.down {
  transform: translate(0, -50px);
}
.scrollIn.right,
.loadIn.right {
  transform: translate(50px, 0);
}
.scrollIn.left,
.loadIn.left {
  transform: translate(-50px, 0);
