  0%, 40% {
    transform: translateY(-20%) scale(1);
  }
  60% {
    transform: translateY(-10%) scale(1);
  }
}
@keyframes leftSlide {
  0% {
    width: 0;
  }
  50% {
    width: 105%;
    transform: scaleX(1);
  }
  70% {
    width: 105%;
    transform: scaleX(1);
  }
  100% {
    width: 105%;
    transform: scaleX(0);
  }
}
